@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

.box {
    position: absolute;
    min-width: 108rem;
    height: 69.813rem;
    left: 0px;
    top: 0px;
    background: #000000;
    /* border: 1px solid red; */
}

h1 {
    position: absolute;
    width: 670px;
    height: 144px;
    left: 79px;
    top: 92px;
    font-weight: 400;
    font-size: 64px;
    line-height: 77px;
    color: #FFFFFF;
}

hr {
    position: absolute;
    width: 276px;
    height: 0px;
    left: 79px;
    top: 244px;
    border: 1px solid #FFFFFF;
}

p {
    position: absolute;
    width: 327px;
    height: 29px;
    left: 79px;
    top: 198px;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;
}

h3 {
    position: absolute;
    width: 416px;
    height: 29px;
    left: 79px;
    top: 277px;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #FF4F4F;
    /* visibility: hidden; */
}

.btn {
    position: absolute;
    left: 52px;
    top: 360px;
}

button {
    margin-left: 27px;
    width: 226px;
    height: 58px;
    background: #FFFFFF;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
}

.button2 {
    background: #000000;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
}